button {
  width: 100px;
  height: 40px;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 5px;
  font-size: 1rem;
  text-decoration: none;
  position: relative;
  left: 40%;
  overflow: hidden;
}

button:hover {
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, .4);
}

